import React from "react";
import {Group, PanelHeader, PanelHeaderBack, Placeholder, RichCell, Avatar, Button, Select, Div} from "@vkontakte/vkui";
import NotifSnack from "./NotifSnack";
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Error from '@vkontakte/icons/dist/24/error';
import getParams from "../functions/getParams";
import getRand from "../functions/getRand";
import usersUpdate from "../functions/usersUpdate";
import areasUpdate from "../functions/areasUpdate";
import check0100 from "../functions/check0100";
import Icon56RecentOutline from '@vkontakte/icons/dist/56/recent_outline';
import Icon28HelpOutline from '@vkontakte/icons/dist/28/help_outline';

class LawsPanel extends React.Component{
    diversionOffset = 60*30;
    constructor(props) {
        super(props);
        this.state = {
            snackbar: ''
        }
        this.diversion = this.diversion.bind(this);
        this.closeSnackbar = this.closeSnackbar.bind(this);
        this.openSnackbar = this.openSnackbar.bind(this);
    }
    closeSnackbar(){
        this.setState({ snackbar: null })
    }
    openSnackbar(g,m){
        this.setState({
            snackbar: <NotifSnack closeSnackbar={this.closeSnackbar} good={g} message={m}/>
        });
    }
    diversion(e){
        let q = this.props.areas;
        let w = q[this.props.curArea];

        w.approval = check0100(w.approval);
        w.happiness = check0100(w.happiness);

        areasUpdate(q);
    }
    render() {
        let h = (new Date()).getHours();
        const header = <PanelHeader
            left={<PanelHeaderBack onClick={() => {this.props.openPanel('main')}}/>}
        >
            Законы
        </PanelHeader>;
        if(h >= 12 && h < 13){
            return (
                <div>
                    {header}
                    <Group description="законы определяют направление развития района. правильное их сочетание быстро выведет к доминации.">
                        <RichCell
                            disabled
                            multiline
                            before={<Avatar size={72} src={'aye'} />}
                            after={<Icon28HelpOutline/>}
                            actions={
                                <Select>
                                    <option>a</option>
                                    <option>y</option>
                                    <option>e</option>
                                </Select>
                            }
                        >
                            Вооруженные силы
                        </RichCell>
                        <RichCell
                            disabled
                            multiline
                            before={<Avatar size={72} src={'aye'} />}
                            after={<Icon28HelpOutline/>}
                            actions={
                                <Select>
                                    <option>a</option>
                                    <option>y</option>
                                    <option>e</option>
                                </Select>
                            }
                        >
                            Торговля
                        </RichCell>
                        <RichCell
                            disabled
                            multiline
                            before={<Avatar size={72} src={'aye'} />}
                            after={<Icon28HelpOutline/>}
                            actions={
                                <Select>
                                    <option>a</option>
                                    <option>y</option>
                                    <option>e</option>
                                </Select>
                            }
                        >
                            Экология
                        </RichCell>
                        <RichCell
                            disabled
                            multiline
                            before={<Avatar size={72} src={'aye'} />}
                            after={<Icon28HelpOutline/>}
                            actions={
                                <Select>
                                    <option>a</option>
                                    <option>y</option>
                                    <option>e</option>
                                </Select>
                            }
                        >
                            Власть
                        </RichCell>
                        <RichCell
                            disabled
                            multiline
                            before={<Avatar size={72} src={'aye'} />}
                            after={<Icon28HelpOutline/>}
                            actions={
                                <Select>
                                    <option>a</option>
                                    <option>y</option>
                                    <option>e</option>
                                </Select>
                            }
                        >
                            Права и свободы
                        </RichCell>
                        <RichCell
                            disabled
                            multiline
                            before={<Avatar size={72} src={'aye'} />}
                            after={<Icon28HelpOutline/>}
                            actions={
                                <Select>
                                    <option>a</option>
                                    <option>y</option>
                                    <option>e</option>
                                </Select>
                            }
                        >
                            Занятость
                        </RichCell>
                        <Div>
                            <Button size="xl">
                                Принять конституцию
                            </Button>
                        </Div>
                    </Group>
                    {this.state.snackbar}
                </div>
            )
        }
        else{
            return <div>
                {header}
                <Placeholder
                    icon={<Icon56RecentOutline/>}
                    header="Закрыто"
                >
                    Принимать законы можно с 12 до 13 часов
                </Placeholder>
            </div>
        }
    }
}

export default LawsPanel;