import {Avatar, Button, Div, Group, Header, Progress, SimpleCell} from "@vkontakte/vkui";
import pivo from "../../img/pv.png";
import React from "react";
import economy from "../../const/economy";

import Icon28MessageOutline from '@vkontakte/icons/dist/28/message_outline';
import Icon28SmileOutline from '@vkontakte/icons/dist/28/smile_outline';
import Icon36MoneyCircleOutline from '@vkontakte/icons/dist/36/money_circle_outline';
import Icon28CubeBoxOutline from '@vkontakte/icons/dist/28/cube_box_outline';
import Icon28Users3Outline from '@vkontakte/icons/dist/28/users_3_outline';
import Icon28SortHorizontalOutline from '@vkontakte/icons/dist/28/sort_horizontal_outline';
import Icon28LikeOutline from '@vkontakte/icons/dist/28/like_outline';
import getParams from "../../functions/getParams";
import areasUpdate from "../../functions/areasUpdate";
import usersUpdate from "../../functions/usersUpdate";

class AreaGroupMain extends React.Component{
    constructor(props) {
        super(props);
        this.takeArea = this.takeArea.bind(this);
    }
    takeArea(){
        let z = this.props.users;
        let x = this.props.user;
        let c = this.props.areas;
        let v = this.props.curArea;

        c[v].owner = parseInt(getParams('vk_user_id'));
        c[v].happiness = 50;
        c[v].approval = 30;

        x.area.push(v);
        z[`U${getParams('vk_user_id')}`] = x;

        areasUpdate(c);
        usersUpdate(z);
    }
    render() {
        let p = this.props;
        let activeArea = p.areas[p.curArea];
        let owner;
        let button = <Div>
            <Button onClick={this.takeArea} size="xl">Захватить власть</Button>
        </Div>;
        if(activeArea.owner === 0){
            owner = {
                photo_200: 'https://firebasestorage.googleapis.com/v0/b/game1-cae49.appspot.com/o/putin.jpg?alt=media&token=5362dd0d-512d-4987-8a26-1a433f8d132e',
                first_name: 'Владимир',
                last_name: 'Пыня'
            }
        }
        else{
            owner = p.users['U'+p.areas[p.curArea].owner];
        }
        return (
            <Group header={<Header mode="secondary">Информация</Header>}>
                <SimpleCell
                    before={<Avatar style={{objectFit: 'cover'}} size={48} src={owner.photo_200} />}
                    after={(p.areas[p.curArea].owner !== parseInt(getParams('vk_user_id')) && p.areas[p.curArea].owner !== 0) ? <Icon28MessageOutline onClick={() => {window.open(`https://vk.me/id${p.areas[p.curArea].owner}`)}}/> : ''}
                    description="управляет районом">{`${owner.first_name} ${owner.last_name}`}
                </SimpleCell>
                <SimpleCell
                    before={<Icon28SmileOutline/>}
                    description="уровень счастья"
                >
                    <Progress key={activeArea.name+'distrhappi'+activeArea.happiness} value={activeArea.happiness}/>
                </SimpleCell>
                <SimpleCell
                    before={<Icon28LikeOutline/>}
                    description="поддержка власти"
                >
                    <Progress key={activeArea.name+'d_approval'+activeArea.approval} value={activeArea.approval}/>
                </SimpleCell>
                <SimpleCell
                    before={<Icon36MoneyCircleOutline width={28} height={28}/>}
                    description="ВВП"
                    style={{display: 'none'}}
                >
                    {activeArea.happiness*activeArea.population/100}
                </SimpleCell>
                <SimpleCell
                    style={{display: 'none'}}
                    before={<Icon28CubeBoxOutline/>}
                    description="тип экономики"
                >
                    {economy[activeArea.economy]}
                </SimpleCell>
                <SimpleCell
                    style={{display: 'none'}}
                    before={<Icon28SortHorizontalOutline/>}
                    description="контракты"
                >
                    53
                </SimpleCell>
                <SimpleCell
                    before={<Icon28Users3Outline/>}
                    description="население"
                >
                    {activeArea.population} тыс.
                </SimpleCell>
                {(activeArea.owner === 0) ? button : ''}
            </Group>)
    }
}

export default AreaGroupMain;