import {Avatar, Header, HorizontalScroll, List, SimpleCell} from "@vkontakte/vkui";
import React from "react";
//"start": "react-scripts start",

import Icon28MoneyCircleOutline from '@vkontakte/icons/dist/28/money_circle_outline';
import Icon28LocationOutline from '@vkontakte/icons/dist/28/location_outline';
import Icon28TargetOutline from '@vkontakte/icons/dist/28/target_outline';
import Icon28ViewOutline from '@vkontakte/icons/dist/28/view_outline';
import Icon28DocumentOutline from '@vkontakte/icons/dist/28/document_outline';

import polyColors from "../const/colors";
import Icon16Done from '@vkontakte/icons/dist/16/done';
import getParams from "../functions/getParams";
import usersUpdate from "../functions/usersUpdate";

class UserHeaderContent extends React.Component{
    constructor(props) {
        super(props);
        this.colorUpdate = this.colorUpdate.bind(this);
    }

    colorUpdate(e){
        let z = this.props.users;
        let x = this.props.user;
        if(x.color === e){return;}
        x.color = e;
        z[`U${getParams('vk_user_id')}`] = x;
        usersUpdate(z);
    }

    render() {
        const itemStyle = {
            flexShrink: 0,
            width: 80,
            height: 80,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
        };

        let p = this.props;
        let owner = p.user;
        return <List>
            <SimpleCell
                before={<Avatar size={48} src={owner.photo_200} />}
                description="лучший управляющий?">{`${owner.first_name} ${owner.last_name}`}
            </SimpleCell>
            <Header mode="secondary">Выбрать цвет</Header>
            <HorizontalScroll>
                <div style={{ display: 'flex' }}>
                    {polyColors.map((v, i) => <div onClick={() => {this.colorUpdate(i)}} style={itemStyle}>
                        <Avatar style={{backgroundColor: v}} size={64}>{i === owner.color ? <Icon16Done fill="#FFFFFF" width={24} height={24}/> : ''}</Avatar>
                    </div>)}
                </div>
            </HorizontalScroll>
            <SimpleCell
                before={<Icon28TargetOutline/>}
                expandable={true}
            >Вооруженные силы</SimpleCell>
            <SimpleCell
                before={<Icon28ViewOutline/>}
                expandable={true}
            >Разведка</SimpleCell>
            <SimpleCell
                before={<Icon28DocumentOutline/>}
                expandable={true}
            >Конституция</SimpleCell>
        </List>
    }
}

export default UserHeaderContent;